export default {
    name:'nimg',
    props:['aid','bid','cid'],
    data:function(){
        return{
            moreimg:{}
        }
    },
    beforeRouteEnter: function(to, from, next) {
    next(function(vm) {
     
        vm.$http.get("/v2-car-getCategoryImageList.html?SerialID="+to.params.aid+"&ImageID="+to.params.bid+"&Page="+to.params.cid+"&PageSize=30&_1531054352382").then(function(response) {
            console.log(response.data)
            vm.moreimg=response.data.data;
      });
    
    });
  }
}



